    line-height: 80px;
    color: #0C0B0B;
  }

  .accordion-content.active p {
    padding: 2rem;
  }

  .slider {
    min-height: 500px;
    margin-top: 5rem;
  }

  .slider-inner {
    gap: 30px;
  }

  .alcuni-progetti .slider {
    min-height: 550px;
  }
}
@media (min-width: 1700px) {
  .slider {
    min-height: 700px;
    margin-top: 5rem;
  }
}